.customs_container__Anim__30hHb{
    position: fixed;
    display: flex;
    flex-direction: row;

    width: 100%;
    height: 100vh;
}

.customs_animation__box__EY-1W{
    flex: 1 1;
    background-color: rgb(100, 100, 179);
}

.customs_animation__content__2lkxa{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    overflow: hidden;
    z-index: 10;
    padding: 1rem;

    border-radius: 5px;
}

.customs_animation__inner__GF9j9{
    display: flex;
    align-items: center;

    height: 50px;
    padding: 5px;
    z-index: 9;
}

.customs_animation__square__RsVIj{
    width: 10px;
    height: 100%;
    background-color: brown;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
}

.customs_animation__text__B1g7S{
    margin-left: 20px;
    font-size: 2.5rem;
}

.customs_animation__button__1pOrq{
    background-color: brown;
    border: 1px solid black;
    border-radius: 5px;
    font-size: 3rem;
    padding: 0.4rem 2rem;
    margin-top: 2rem;
    cursor: pointer;
    transition: 0.5;
    z-index: 10;
}

.customs_animation__contentBackground__tJuWJ{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-color: rgba(201, 201, 201, 0.6);
    z-index: 2;
}

.customs_animation__image__1WMEp{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
    object-fit: cover;
}

/* MENU */
.customs_menu__container__3ugGQ{
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 100vh;
    width: 3rem;

    z-index: 20;
}

.customs_menu__tile__j5m51{
    height: 60px;
    width: 20px;
    margin-top: 20px;

    border: 1px solid black;
    border-radius: 2rem;
    box-shadow: 2px 2px black;
    background-color: white;
    cursor: pointer;
}

/* CONTACT */
.customs_tile__3rPJA{
    padding: 2rem;
}

